.mangopay-block-description {
    .dokan-mangopay-credit-cards {
        &-content {
            width: 100%;
        }
    }
}

.dokan-mangopay-registered-cards {

    &-loader {
        width: 100%;
        height: 60px;
        margin-top: -20px;
        display: flex;
        justify-content: center;
        align-items: center;

        &-label {
            margin-top: 40px;
        }
    }

    &-box {
        &-empty {
            border: 1px solid #723B14;
            background: #FDFDEA;
            border-radius: 3px;

            &-message {
                color: #723B14;
                margin: 0 !important;
                padding: 15px;
            }
        }

        .dokan-mangopay-add-new-box {
            margin-top: 20px;

            .dokan-mangopay-add-card-box-divider {
                margin: 20px 0 0 0;
            }
            .dokan-mangopay-expire-select {
                display: flex;

                select {
                    width: 100%;
                }
                span {
                    width: 50px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                }
            }
            .dokan-mangopay-add-card-box-button {
                display: flex;
                justify-content: space-between;
                margin-top: 20px;
            }
        }
    }
}

.dashicons-trash {
    &:hover {
        color: #9f191f;
    }
}
